import React from 'react'
import Link from 'gatsby-link'
import Navigoat from '../layouts/navigoat.js'

export default () =>
  <div>
    <Navigoat active_tab='contact'/>
    <div className = "container" id="top-padded">
  		<div className = "row">
  			<div className = "col-lg-9">
  				<div className = "panel panel-default">
  					<div className = "panel-body">
  						<div className = "page-header" id="center">
  							<h2 id="center">Contact</h2>
  							<hr width="30%" id="cute-rule"/>
  							<br/>
  							<span className="glyphicon glyphicon-envelope"></span>
  							<br/>
  							pientars [ a-t ] gatech [ d-o-t ] edu
  							<br/><br/><br/>
  							<span className="glyphicon glyphicon-map-marker"></span>
  							<br/>
  							266 Ferst Drive, Atlanta, GA 30332, USA
  							<hr/>
  							<p id="julius">Alas, poor Yorick! I knew him, Horatio, a fellow of infinite jest, of most excellent fancy.</p>
  						</div>
  					</div>
  				</div>
  			</div>
  		</div>
  	</div>
  </div>



