import React from 'react'
import Link from 'gatsby-link'
import Navigoat from '../layouts/navigoat.js'


export default () =>
  <div>
    <Navigoat active_tab='gallery'/>
    <div className = "container" id="top-padded">
      <div className="row" id="art_food_selector">
        <div className="col-sm-6 feature">
          <div className="panel panel-default">
            <div className="panel-heading">
              <Link id="simple-btn" to="/art/">Check it out!</Link>
            </div>
            <div className="panel-body">
              <Link to="/art/"><img src="../imgs/gallery/ross_thumb.png" alt="Art I guess" className="img-circle"/></Link>
              <p>"An artist's only concern is to shoot for some kind of perfection, and on his own terms, not anyone else's."</p>

            </div>
          </div>
        </div>
        <div className="col-sm-6">
        <div className="panel panel-default">
            <div className="panel-heading">
              <Link id="simple-btn" to="/food/">Great Job!</Link>
            </div>
            <div className="panel-body">
              <Link to="/food/"><img src="../imgs/gallery/food_thumb.png" alt="Food maybe?" className="img-circle"/></Link>
              <p>“You've got to talk about it too. And you've got to talk about it to somebody who understands that kind of food.”</p>

            </div>
          </div>
        </div>
      </div>
    </div>
  </div>



