import React from 'react'
import Link from 'gatsby-link'
import Navigoat from '../layouts/navigoat.js'
import ProjectPage from '../layouts/pr_page.js'

export default () =>
  <div>
    <Navigoat active_tab='projs'/>
    <ProjectPage title = 'STEPS: Towards Quickly Understanding Electrical Network Behavior'
                 paper_title = 'STEPS: A Spatio-temporal Electric Power Systems Visualization'
                 authors = 'Robert Pienta, Leilei Xiong, Santiago Grijalva, Duen Horng Chau, and Minsuk Kahng'
                 abstract = 'As the bulk electric grid becomes more complex, power system operators and engineers have more information to process and interpret than ever before. The information overload they experience can be mitigated by effective visualizations that facilitate rapid and intuitive assessment of the system state. With the introduction of non- dispatchable renewable energy, flexible loads, and energy storage, the ability to temporally explore system states becomes critical. This paper introduces STEPS, a new 3D Spatio-temporal Electric Power Systems visualization tool suitable for steady-state operational applications.'
                 paper_linky = '../data/16-iui-steps.pdf'
                 paper_img = '../imgs/projs/pr-steps-cj.png'
                 img_caption = "STEP'S main view is composed of (A) the control panel and (B) the 3D network view. Here the 3D network view illustrates the changes of a desired variable, through time, for each bus. The control panel allows users to select the visualized attributes, visual bus and line characteristics (such as opacity or height), filter out values, adjust the cameras, and many other features."
                 video_linky = 'https://www.youtube.com/embed/5wyRryD07As?rel=0'
    />

  </div>



