import React from 'react'
import Link from 'gatsby-link'
import Navigoat from '../layouts/navigoat.js'

export default () =>
  <div>
    <Navigoat active_tab='projs'/>
    <div className = "container" id="top-padded">
      <div className="row" id="art_food_selector">

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">VISAGE</h3>
            </div>
            <div className="panel-body">
              <Link to="/pr_visage/"><img className="fit-img" src="../imgs/projs/visage.png" alt="VISAGE"/></Link>
              <p>Visual, interactive query construction without coding!</p>
            </div>
          </div>
        </div>

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">VIGOR</h3>
            </div>
            <div className="panel-body">
              <Link to="/pr_vigor/"><img className="fit-img"  src="../imgs/projs/vigor.png" alt="VIGOR"/></Link>
              <p>Quickly visualize thousands of subgraph results!</p>
            </div>
          </div>
        </div>

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">MAGE</h3>
            </div>
            <div className="panel-body">
              <Link to="/pr_mage/"><img className="fit-img"  src="../imgs/projs/mage.png" alt="MAGE"/></Link>
              <p>Find </p>
            </div>
          </div>
        </div>

      </div>
      <div className="row" id="art_food_selector">

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">FACETS </h3>
            </div>
            <div className="panel-body">
              <Link to="/pr_facets/">
              <img className="fit-img"  src="../imgs/projs/facets.png" alt="facets"/>
              </Link>
              <p>Locally explore massive graphs faster than ever.</p>
            </div>
          </div>
        </div>

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">GraSP</h3>
            </div>
            <div className="panel-body">
              <Link to="/pr_grasp/">
              <img className="fit-img"  src="../imgs/projs/grasp.png" alt="GraSP"/>
              </Link>
              <p>Fast streaming graph partitioning. You can now k-partition your largest graphs in minutes using the GraSP distributed build.</p>
            </div>
          </div>
        </div>

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">STEPS</h3>
            </div>
            <div className="panel-body">
              <Link to="/pr_steps/">
                <img className="fit-img"  src="../imgs/projs/steps.png" alt="PSERC"/>
              </Link>
              <p>STEPS provides high level overviews for spatio-temporal electrical power system dynamics. </p>
            </div>
          </div>
        </div>

      </div>
      <div className = "row" id="art_food_selector">

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">Bird Migration Tracking (UNDER CONSTRUCTION)</h3>
            </div>
            <div className="panel-body">
              <a href="https://github.com/Saganaut/com4susBOIDS"><img className="fit-img"  className="img-circle" src="../imgs/projs/tree_swallow.jpg" alt="eBird Tracking"/></a>
              <p>Tracking bird migration with network time-correlations.</p>
            </div>
          </div>
        </div>

        <div className="col-sm-3">
          <div className="panel panel-default">
            <div className="panel-heading">
              <h3 className="panel-title">Learning QWOP (UNDER CONSTRUCTION)</h3>
            </div>
            <div className="panel-body">
              <a href=""><img className="fit-img"  className="img-circle" src="../imgs/projs/qwop.png" alt="Learning QWOP"/></a>
              <p>Using genetic algorithms and reinforcement learning to play QWOP. <em>Coming Soon.</em></p>
            </div>
          </div>
      </div>

      <div className="col-sm-3">
        <div className="panel panel-default">
          <div className="panel-heading">
            <h3 className="panel-title">Carina (UNDER CONSTRUCTION)</h3>
          </div>
          <div className="panel-body">
            <Link to="/pr_carina/">
            <img className="fit-img"  src="../imgs/projs/carina.png" alt="Carina"/>
            </Link>
            <p>Drawing, customizing, and visualizing large networks has never been easier.</p>
          </div>
        </div>
      </div>
      </div>
    </div>
  </div>



